import BasicKnowwledge from "./BasicKnowledge";

// CocosCreator.app/Contents/Resources/static/template/new-script.ts
const {ccclass, property} = cc._decorator;

@ccclass
export default class HomePage extends cc.Component {

    @property(cc.Button)
    button_dataType: cc.Button;

    @property(cc.Label)
    label: cc.Label = null;

    @property
    text: string = 'hello';

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.createButtonByCode();
        // 设置分辨率
        // EXACT_FIT 铺满可变形
        // NO_BORDER 拉满裁剪多出部分
        // SHOW_ALL 以允许留白形式展示整个场景
        // FIXED_HEIGHT 高度合适
        // FIXED_WIDTH 宽度合适
        cc.view.setDesignResolutionSize(720, 1280, cc.ResolutionPolicy.FIXED_WIDTH);
        // 获取分辨率
        const designSize = cc.view.getDesignResolutionSize();
        console.log('design size:', designSize.width, designSize.height);
        
    }

    start () {

    }

    // update (dt) {}

    createButtonByCode() {
        const buttonNode = new cc.Node(); // 创建button的 node节点
        buttonNode.name = 'data_type_button_node';
        buttonNode.parent = this.node; // 添加到当前节点
        buttonNode.setPosition(new cc.Vec2(0, 0));
        buttonNode.setContentSize(new cc.Size(200,  50));

        const button = buttonNode.addComponent(cc.Button); // 为buttonNode节点添加Button组件

        const labelNode = new cc.Node(); // 创建按钮文本的节点
        labelNode.name = 'data_type_button_label_node';
        labelNode.parent = buttonNode;
        labelNode.setPosition(new cc.Vec2(0, 0)); // 设置 Label 位置居中
        labelNode.setContentSize(buttonNode.getContentSize()); // 设置 Label 大小与按钮相同

        const label = labelNode.addComponent(cc.Label);
        label.string = '点击我测试数据类型';

        buttonNode.on('click', this.onDataTypeButtonClick, this);
    
        const clickEventHandler = new cc.Component.EventHandler(); // 创建点击事件处理器
        // clickEventHandler.target = this.node;
        // clickEventHandler.component = 'data_type_test_script';
        // clickEventHandler.handler = 'onDataTypeButtonClick';
        // button.clickEvents.push(clickEventHandler);
    }

    onDataTypeButtonClick() {
        BasicKnowwledge.sharedInstance.testDataType();
        BasicKnowwledge.sharedInstance.testNumber();
    }
}
